import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Logo from './partials/Logo';
import Button from '../elements/Button';

const propTypes = {
  navPosition: PropTypes.string,
  hideNav: PropTypes.bool,
  hideSignin: PropTypes.bool,
  bottomOuterDivider: PropTypes.bool,
  bottomDivider: PropTypes.bool,
};

const defaultProps = {
  navPosition: '',
  hideNav: false,
  hideSignin: false,
  bottomOuterDivider: false,
  bottomDivider: false,
};

const Header = ({ className, navPosition, hideNav, hideSignin, bottomOuterDivider, bottomDivider, ...props }) => {
  const [isActive, setIsactive] = useState(false);

  const nav = useRef(null);
  const hamburger = useRef(null);

  useEffect(() => {
    isActive && openMenu();
    document.addEventListener('keydown', keyPress);
    document.addEventListener('click', clickOutside);
    return () => {
      document.removeEventListener('keydown', keyPress);
      document.removeEventListener('click', clickOutside);
      closeMenu();
      markLang();
    };
  });

  const openMenu = () => {
    document.body.classList.add('off-nav-is-active');
    nav.current.style.maxHeight = nav.current.scrollHeight + 'px';
    setIsactive(true);
  };

  const closeMenu = () => {
    document.body.classList.remove('off-nav-is-active');
    nav.current && (nav.current.style.maxHeight = null);
    setIsactive(false);
  };

  const keyPress = (e) => {
    isActive && e.keyCode === 27 && closeMenu();
  };

  const clickOutside = (e) => {
    if (!nav.current) return;
    if (!isActive || nav.current.contains(e.target) || e.target === hamburger.current) return;
    closeMenu();
  };

  const classes = classNames('site-header', bottomOuterDivider && 'has-bottom-divider', className);

  const markLang = () => {
    const currentLang = localStorage.getItem('CURRENT_LANG');
    document.getElementById('slash').style.color = '#e65c00';
    if (currentLang === 'en') {
      document.getElementById('en').style.color = 'black';
      document.getElementById('es').style.color = 'white';
    } else {
      document.getElementById('en').style.color = 'white';
      document.getElementById('es').style.color = 'black';
    }
  };

  const setLang = () => {
    const currentLang = localStorage.getItem('CURRENT_LANG');
    if (currentLang === 'en') localStorage.setItem('CURRENT_LANG', 'es');
    else localStorage.setItem('CURRENT_LANG', 'en');
    window.location.reload();
  };

  const localize = JSON.parse(localStorage.getItem('localize'));

  return (
    <header {...props} className={classes}>
      <div className="container">
        <div className={classNames('site-header-inner', bottomDivider && 'has-bottom-divider')}>
          <Logo />
          {!hideNav && (
            <>
              <button ref={hamburger} className="header-nav-toggle" onClick={isActive ? closeMenu : openMenu}>
                <span className="screen-reader">Menu</span>
                <span className="hamburger">
                  <span className="hamburger-inner"></span>
                </span>
              </button>
              <nav ref={nav} className={classNames('header-nav', isActive && 'is-active')}>
                <div className="header-nav-inner">
                  <ul className={classNames('list-reset text-xs', navPosition && `header-nav-${navPosition}`)}>
                    <li>
                      <a href="https://drive.google.com/file/d/1z0OklUPn9QgxlqxDafn0an76wltqbQn-/view?usp=sharing" target="_blank">
                        {localize.documentation}
                      </a>
                    </li>
                  </ul>
                  {!hideSignin && (
                    <ul className="list-reset header-nav-right">
                      <li>
                        <Button to="#0" className="button button-primary button-wide-mobile button-sm" onClick={setLang}>
                          <span id="en">EN</span>
                          <strong>
                            <span id="slash">/</span>
                          </strong>
                          <span id="es">ES</span>
                        </Button>
                      </li>
                    </ul>
                  )}
                </div>
              </nav>
            </>
          )}
        </div>
      </div>
    </header>
  );
};

Header.propTypes = propTypes;
Header.defaultProps = defaultProps;

export default Header;
